import { ApacheLicense, BoostLicense, BSD2License, BSD3License, CCZ1License, Eclipse2License, GNUAGPL3License, GNUGPL2License, GNUGPL3License, GNULGPL2_1License, MITLicense, MozillaLicense, Unlicense, } from '../lib/licenses/index.js';
// Function that returns a license badge based on which license is passed in
// If there is no license, return an empty string
function renderLicenseBadge(license) {
    if (license !== 'none') {
        return "![Project License Badge](https://img.shields.io/badge/license-".concat(license, "-brightgreen)");
    }
    else {
        return '';
    }
}
// Function that returns the license link
// If there is no license, return an empty string
var renderLicense = function (data) {
    var year = new Date().getFullYear();
    switch (data.license) {
        case 'Apache':
            return ApacheLicense(year, data.user);
        case 'BSD 2-Clause':
            return BSD2License(year, data.user);
        case 'BSD 3-Clause':
            return BSD3License(year, data.user);
        case 'Boost':
            return BoostLicense();
        case 'Creative Commons Universal':
            return CCZ1License();
        case 'Eclipse':
            return Eclipse2License();
        case 'GNU AGPL':
            return GNUAGPL3License(year, data.user);
        case 'GNU GPL v2':
            return GNUGPL2License(year, data.user);
        case 'GNU GPL v3':
            return GNUGPL3License(year, data.user);
        case 'GNU LGPL':
            return GNULGPL2_1License(year, data.user);
        case 'Mozilla':
            return MozillaLicense();
        case 'MIT':
            return MITLicense(year, data.user);
        case 'Unlicense':
            return Unlicense();
        default:
            return '';
    }
};
// Function that returns the license section of README
// If there is no license, return an empty string
function renderLicenseSection(data) {
    return "\n  ## License\n  \n    ".concat(data.license !== 'none'
        ? renderLicense(data)
        : 'There has not been any license set for this project.', "\n    ");
}
// Function to generate markdown for README
var generateMarkdown = function (data) {
    return "\n  # ".concat(data.title, "  ").concat(renderLicenseBadge(data.license), "\n\n  ## Description\n\n  ").concat(data.description, "\n\n  ## Table of Contents\n\n  * [Installation](#Installation)\n  * [Usage](#Usage)\n  * [Contributing](#Contributing)\n  * [Tests](#Tests)\n  * [License](#license)\n  * [Questions](#Questions)\n\n  ***\n\n  ## Installation\n\n  ").concat(data.install, "\n\n  ## Usage\n\n  ").concat(data.usage, "\n\n  ## Contributing\n\n  ").concat(data.contribution, "\n\n  ## Tests\n\n  ").concat(data.test, "\n  \n  ").concat(renderLicenseSection(data), "\n  \n  ## Questions\n\n  If you have any questions about the project you can reach out to me via email or GitHub with the information below. \n\n  >Email: ").concat(data.email, " \n\n  >GitHub : [").concat(data.user, "](https://github.com/").concat(data.user, ")\n\n  ");
};
export { generateMarkdown, renderLicense };
